@import url(https://fonts.googleapis.com/css?family=Montserrat);
* {
  font-family: 'Montserrat', 'sans-serif';
  color: white;
}

.navBackground {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  width: 300px;
  background: #313131;
}

nav {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  width: 300px;
  z-index: 1000;
}

button {
  outline: none;
  border: none;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  cursor: pointer;
  position: absolute;
  top: 15px;
  left: 15px;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  background: transparent;
}

ul,
li {
  margin: 0;
  padding: 0;
}

ul {
  padding: 25px;
  position: absolute;
  width: 230px;
}

li {
  list-style: none;
  margin-bottom: 20px;
  display: flex;
  align-items: center;
  cursor: pointer;
}

.icon-placeholder {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  flex: 40px 0;
  margin-right: 20px;
}

.text-placeholder {
  border-radius: 5px;
  width: 200px;
  height: 20px;
  flex: 1 1;
}

.refresh {
  padding: 10px;
  position: absolute;
  background: rgba(0, 0, 0, 0.4);
  border-radius: 10px;
  width: 20px;
  height: 20px;
  top: 10px;
  right: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}

#root {
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
}

body {
  margin: 0;
  font-family: 'Montserrat', 'sans-serif';
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  -webkit-backdrop-filter: blur(5px);
          backdrop-filter: blur(5px);
}

code {
  font-family: 'Montserrat', 'sans-serif';
}

::-webkit-scrollbar {
  width: 10px;
  height: 10px;
}

::-webkit-scrollbar-thumb {
  background-color: #424242;
}
.coin-icon {
    width: 25px;
}

.nav-item:hover {
    background-color: #202020;
}

.nav-text {
    text-decoration: none;
}

.nav-text-title {
    padding-top: 2px;
    text-decoration: none;
    font-size: 22px;
    color: white;
}

.nav-text-title:hover {
    color:rgb(151, 151, 151);
}

.banner {
    height: 200px;
    width: 100%;
    margin-top: 50px;
    justify-content: space-around;
    background-color: #202020 !important;
}

.red {
    color: rgb(200, 0, 0);
}

.green {
    color: rgb(0, 170, 0);
}
